@font-face {
    font-family: 'toreadoreregular';
    src: url('fonts/toreadore-webfont.woff2') format('woff2'),
         url('fonts/toreadore-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'gunplayregular';
    src: url('fonts/gunplay_rg-webfont.woff2') format('woff2'),
         url('fonts/gunplay_rg-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
@media (min-width: 320px){
	h1{
	  font-family: 'toreadoreregular', Impact, Charcoal, sans-serif;
    font-size: 36px;
    text-align: right;
    margin: 6px;
    color: #FFFFFF;
    text-shadow: 2px 2px rgba(00,00,00, 0.5);
	}
	h2{
  	margin:20px 5px;
    font-size: 26px;
	}
	h2, h3, h4, blockquote{
		font-family: 'gunplayregular', Impact, Charcoal, sans-serif;
  }
  blockquote, h3{
    font-size:22px;
  }
  h3, h4{
    margin:20px 5px;
  }
  h4{
    font-size: 18px;
  }
	body{
		font-family: Geneva, Verdana, sans-serif;
		font-size: 16px;
		background-color: #FEF9F0;
    margin: 0;
    padding: 0;
	}
	p{
		margin:20px 5px;
	}
	footer{
		width: 100%;
		background-color: #761C1C;
	}
	.next{
		display: none;
	}
  .talking{
    margin-left: 10%;
    font-size: 14px;
  }
  .backgroundRed{
    width: 100%;
    background-color: #761C1C;
    color: #FEF9F0;
  }
  .info{
    cursor: pointer;
    text-align: center;
    font-size: 18px;
    font-style: italic;
  }
  .FF{
    text-decoration: underline;
  }
  .link{
    text-decoration: underline;
    color: #761C1C;
  }
  .twoButtons{
    display: flex;
  }
  .columnButton{
    flex: 50%;
    margin: 30px 40px;
    display: inline-flex;
  }
  button{
    padding: 30px;
    font-size: 30px;
    font-style: italic;
    border-radius: 4px;
    background-color: #FEF9F0;
  }
  button:hover{
    background-color: #761C1C;
    color: #FFFFFF;
  }
  //Navigation - Hamburger
  .smallNav{
  	display: flex;
  }
  .header{
  	flex:80%;
  }
  nav{
    width: 100%;
    background-color: #761C1C;
  }
  .hamburger{
    display: block;
    position: relative;
    flex:20%;
  }
  .bar1, .bar2, .bar3{
    width: 30px;
    height: 5px;
    background-color: #FFFFFF;
    margin: 6px 5px;
    transition: 0.4s;
  }
  .change .bar1{
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
  }
  .change .bar2{
    opacity: 0;
  }
  .change .bar3{
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
  }
  header nav ul{
    width: 100%;
    list-style-type: none;
  }
  header nav ul li{
    text-align: left;
    padding: 10px;
    font-family: 'gunplayregular', Impact, Charcoal, sans-serif;
  }
  header nav ul li a{
    text-decoration: none;
    color: #FFFFFF;
  }
  .relativeHeader{
    position: relative;
  }
  .textImg{
  	margin-top:-55px;
  }
  .hide {
    display: none;
  }
  .hidden {
    display: none;
  }
  .footer2{
  	display: flex;
  }
  //Images
  .mainImg{
  	display: flex;
  	flex-direction: column;
  }
  .smallImg{
    width: 100%;
    height: auto;
    margin-top: 3px;
  }
  .smallPlane{
  	width: 100%;
  	opacity: .1;
  	-webkit-transform: rotate(-7deg) translate(60px, -50px);
    transform: rotate(-7deg) translate(60px, -50px);
  }
  .largePlaneRight, .largePlaneLeft, .fa-fighter-jet{
    display: none;
  }
  .fontAwesome{
    display: none;
  }
  .twoImg{
    display: flex;
    border-bottom-style: solid;
    border-bottom-color: #761C1C;
    border-bottom-width: 10px;
    border-top-style: solid;
    border-top-color: #761C1C;
    border-top-width: 10px;
  }
  .column{
    flex: 50%;
  }
  #pilots{
    display: block;
  }
  .redLine{
    width: 100%;
    margin-top:-30px;
  }
  .notmainLine{
    width: 100%;
  }
  .icons{
  	flex: 33.33%;
  	width:10%;
  	height:10%;
  	margin: 3px;
  }
  //Lists
  ol li{
    margin: 20px auto;
  }
  .bigger{
    font-size: 20px;
  }
  .show{
    display: block;
  }
}
@media (min-width: 600px){
  body{
		font-size: 20px;
	}
  h2{
    font-size: 40px;
  }
  h3, blockquote{
    font-size: 30px;
  }
  h4{
    font-size: 20px;
  }
  .talking{
    font-size: 17px;
  }
}
@media (min-width: 768px){
  .container{
    display: grid;
  }
  .content, .content2{
    margin: auto 10%;
  }
	body{
		font-size: 20px;
	}
  h2, blockquote{
    font-size: 40px;
  }
  p{
    margin: 30px auto;
  }
  .smallPlane{
    display: none;
  }
  .textImg{
    margin-top: 20px;
  }
  .twoImg{
    border-top-width: 20px;
    border-bottom-width: 20px;
  }
  .twoText{
    display: flex;
  }
  .columnText{
    flex: 50%;
    margin: 30px 40px;
    display: inline-flex;
  }
  .columnButton{
    margin: auto 20%;
  }
  .fontAwesome{
    display: inline-flex;
  }
  .fa-fighter-jet{
    color: #761C1C;
    font-size: 30px;
  }
  .next{
    display: inline-block;
    color: #FFFFFF;
    font-family: 'gunplayregular', Impact, Charcoal, sans-serif;
    flex: 25%;
  }
  .icons{
  	flex: 25%;
    width: 50%;
  	margin: 3px;
  }
}
@media (min-width: 1024px){
	h1{
		font-size: 86px;
  	text-align:center;
  	left: 0;
  	right:0;
	}
  .headerDown{
    position: absolute;
    top:12%;
  }
  .content{
    margin: auto 30% auto 5%;
  }
  .twoText{
    margin: auto 30% auto 5%;
  }
  .content2{
    margin: auto 5% auto 30%;
  }
  body{
    margin: 0;
    padding: 0;
  }
  //Images
  .largePlaneRight{
    display: block;
    width: 50%;
  	opacity: .2;
    position:absolute;
    -webkit-transform: rotate(205deg) translate(-90%, 100%);
    transform: rotate(205deg) translate(-90%, 100%);
  }
  .largePlaneLeft{
    display: block;
    width: 50%;
  	opacity: .2;
    position:absolute;
  	-webkit-transform: rotate(-205deg) translate(40%, 110%);
    transform: rotate(-205deg) translate(40%, 110%);
  }
  //Navigation
  header nav{
    width: 100%;
    background-color: #761C1C;
  }
  header nav ul{
    display: inline-block;
    text-align: center;
    margin: 0 auto;
    padding: 0;
  }
  header nav ul li{
    list-style: none;
    display: inline-block;
    font-size: 15px;
    padding: 20px 10px;
    transition-duration: .5s;
  }
  header nav ul li:hover{
    cursor:pointer;
    background-color: #000000;
  }
  header nav ul li ul{
    visibility: hidden;
    opacity: 0;
    position: absolute;
    transition: all .5s ease;
    margin-top: 1rem;
    left: 0;
    display: none;
    background-color: #000000;
  }
  nav ul li:hover > ul, nav ul li ul:hover{
    visibility: visible;
    opacity: 1;
    display: block;
  }
  nav ul li ul li{
    clear: both;
    display: block;
  }
  .hide {
    display: inline-block;
  }
  .hamburger{
    display: none;
  }
  //Images
  //Lists
  .top{
    margin-top: 70px;
  }
@media (min-width: 1600px){
	h1{
		font-size:115px;
	}
	header nav ul{
		font-size: 20px;
	}
	header nav ul li{
		font-size: 20px;
	}
}
}
